// 
import config from '@/uni_modules/uni-id-pages/config.js';
// uni-id
const uniIdCo = uniCloud.importObject("uni-id-co", {
	customUI: true
})
// 
const {
	loginTypes,
	debug
} = config

export default async function() {

	// 
	if (debug) {
		// 1. uni-id-pages
		//
		let {
			supportedLoginType
		} = await uniIdCo.getSupportedLoginType()
		console.log("supportedLoginType: " + JSON.stringify(supportedLoginType));
		//
		let data = {
			smsCode: 'mobile-code',
			univerify: 'univerify',
			username: 'username-password',
			weixin: 'weixin',
			qq: 'qq',
			xiaomi: 'xiaomi',
			sinaweibo: 'sinaweibo',
			taobao: 'taobao',
			facebook: 'facebook',
			google: 'google',
			alipay: 'alipay',
			apple: "apple"
		}
		//
		let list = loginTypes.filter(type => !supportedLoginType.includes(data[type]))
		if (list.length) {
			console.error(
				`:${list.join('')};"/uni_modules/uni-config-center/uniCloud/cloudfunctions/common/uni-config-center/uni-id/config.json"`
			);
		}
	}













	//3. clientDB
	// clientDB
	const db = uniCloud.database()
	db.on('error', onDBError)
	//clientDB
	function onDBError({
		code, // https://uniapp.dcloud.net.cn/uniCloud/clientdb?id=returnvalue
		message
	}) {
		// console.error('onDBError', {code,message});
	}
	// clientDB
	//db.off('error', onDBError)


	//4. push_clientiddevice
	if (uniCloud.onRefreshToken) {
		uniCloud.onRefreshToken(() => {
			// console.log('onRefreshToken');
			if (uni.getPushClientId) {
				uni.getPushClientId({
					success: async function(e) {
						// console.log(e)
						let pushClientId = e.cid
						// console.log(pushClientId);
						let res = await uniIdCo.setPushCid({
							pushClientId
						})
						// console.log('getPushClientId', res);
					},
					fail(e) {
						console.log(e)
					}
				})
			}
		})
	}
}
